.card {
    border-radius: 2px;
    height: 360px;
    margin: 1rem;
    position: relative;
    width: 345px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    background: #E9E9FF;
    border-radius: 6px; 
  }
  
  .img-container {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
  }

  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
  }
.wrapper {
    margin-top:10px;
    margin-bottom: 20px; 
    background: #CAC9FD;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 20px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    border-radius: 6px; 
  }
.jumbotron {
    text-align: center;
    margin-bottom: 30px;
    background: #E9E9FF;
  }
  
  .navbar {
    background: #E9E9FF;  
    border-radius: 6px; 
    margin-bottom: 20px;
  }

